import { CHANGE_EASY, CHANGE_MED, CHANGE_HARD, CHANGE_EX } from "./types";

const prependZero = (num) => {
  if (num.toString().length == 1) {
    return "0" + num;
  } else if (Number.isInteger(num) == false) {
    return Math.round(num);
  } else {
    return num;
  }
};

export const changeEasy = (time, num) => {
  return {
    type: CHANGE_EASY,
    payload: {
      minu: prependZero(Math.floor(((60 - time) * num) / 60)),
      secs: prependZero(
        (((60 - time) * num) / 60 - Math.floor(((60 - time) * num) / 60)) * 60
      ),
    },
  };
};

export const changeMed = (time, num) => {
  return {
    type: CHANGE_MED,
    payload: {
      minu: prependZero(Math.floor(((60 - time) * num) / 60)),
      secs: prependZero(
        (((60 - time) * num) / 60 - Math.floor(((60 - time) * num) / 60)) * 60
      ),
    },
  };
};

export const changeHard = (time, num) => {
  return {
    type: CHANGE_HARD,
    payload: {
      minu: prependZero(Math.floor(((60 - time) * num) / 60)),
      secs: prependZero(
        (((60 - time) * num) / 60 - Math.floor(((60 - time) * num) / 60)) * 60
      ),
    },
  };
};

export const changeEx = (count) => {
  return {
    type: CHANGE_EX,
    payload: count,
  };
};
